
"use client";

import { useState, useEffect, useCallback } from 'react';
import { authenticateTelegramUser } from '@/lib/api';
import type { AppUser } from '@/lib/types';
import MainAppLayout from './main-app-layout';
import LoadingSpinner from './loading-spinner';
import ErrorMessage from './error-message';
import { Button } from './ui/button';

// Extend Window interface for Telegram WebApp
declare global {
  interface Window {
    Telegram?: {
      WebApp: {
        initData: string;
        initDataUnsafe: {
          user?: {
            id: number;
            first_name?: string;
            last_name?: string;
            username?: string;
            language_code?: string;
            is_premium?: boolean;
          };
        };
        ready: () => void;
        expand: () => void;
        onEvent: (eventType: string, eventHandler: () => void) => void; // Added onEvent based on prompt
        offEvent: (eventType: string, eventHandler: () => void) => void; // Added offEvent for completeness
        // Add other methods if needed
      };
    };
  }
}

export default function AuthWrapper() {
  const [authStatus, setAuthStatus] = useState<'pending' | 'authenticated' | 'error' | 'no-telegram'>('pending');
  const [user, setUser] = useState<AppUser | null>(null);
  const [error, setError] = useState<string | null>(null);

  const performAuth = useCallback(async () => {
    setAuthStatus('pending'); 
    setError(null);

    if (typeof window !== 'undefined' && window.Telegram && window.Telegram.WebApp) {
      const WebApp = window.Telegram.WebApp;

      WebApp.ready(); // Inform Telegram app that WebApp is ready
      WebApp.expand(); // Expand the Web App view

      const handleTelegramAppReady = async () => {
        try {
          const initData = WebApp.initData;

          if (!initData) { 
            console.warn("AuthWrapper: Telegram initData is missing or empty inside onEvent('ready') callback.");
            setError("Telegram initData is missing. Cannot authenticate.");
            setAuthStatus('error');
            // Optional: unregister listener if 'ready' is a one-time event
            // if (WebApp.offEvent) WebApp.offEvent('ready', handleTelegramAppReady);
            return;
          }

          const authedUser = await authenticateTelegramUser(initData);
          setUser(authedUser);
          setAuthStatus('authenticated');
        } catch (e: any) {
          console.error("Authentication error inside onEvent('ready') callback:", e);
          setError(e.message || "An unknown authentication error occurred.");
          setAuthStatus('error');
        } finally {
          // Optional: unregister listener if 'ready' is a one-time event
          // It's good practice to unregister event listeners when they are no longer needed,
          // especially if the event could fire multiple times and the handler is only meant to run once.
          // For auth, it's typically a one-shot process.
          // if (WebApp.offEvent) WebApp.offEvent('ready', handleTelegramAppReady);
        }
      };
      
      // As per user requirement, use onEvent("ready", ...) to get initData.
      // Note: 'ready' is not a standard event type for WebApp.onEvent in official Telegram documentation.
      // Standard events include 'themeChanged', 'viewportChanged', 'mainButtonPressed', etc.
      // This implementation assumes 'ready' will be triggered in the user's specific environment
      // and that initData will be reliably populated at that point.
      if (typeof WebApp.onEvent === 'function') {
        try {
           // Using @ts-ignore because 'ready' is not a standard event type for onEvent
           // if your Telegram environment guarantees this event, this is fine.
           // @ts-ignore 
          WebApp.onEvent('ready', handleTelegramAppReady);
        } catch (e) {
          console.error("Error registering Telegram onEvent('ready'):", e);
          setError("Failed to initialize Telegram event listener. The 'ready' event might not be supported or correctly configured.");
          setAuthStatus('error');
        }
      } else {
        console.warn("AuthWrapper: window.Telegram.WebApp.onEvent is not a function. Cannot set up event-based auth.");
        setError("Telegram WebApp.onEvent method is not available. This app requires it for authentication.");
        setAuthStatus('error');
      }

    } else {
      // Handle cases where Telegram WebApp object is not available
      setTimeout(() => {
        if (!(typeof window !== 'undefined' && window.Telegram && window.Telegram.WebApp)) {
          console.warn("AuthWrapper: Telegram WebApp object not found after delay. App likely opened outside Telegram.");
          setAuthStatus('no-telegram');
        } else {
          // If Telegram object appeared, try authentication again
          performAuth();
        }
      }, 1000); 
    }
  }, []); 

  useEffect(() => {
    performAuth();
  }, [performAuth]);

  if (authStatus === 'pending') {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen p-4">
        <LoadingSpinner size={48} />
        <p className="mt-4 text-muted-foreground font-headline">Authenticating...</p>
      </div>
    );
  }

  if (authStatus === 'no-telegram') {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen p-4 text-center">
        <ErrorMessage message="Telegram Environment Not Detected" />
        <p className="mt-2 text-muted-foreground font-body">
          Please open this Web App from Telegram.
        </p>
      </div>
    );
  }

  if (authStatus === 'error' || !user) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen p-4 text-center">
        <ErrorMessage message="Authentication Failed" details={error || "Could not authenticate your session."} />
        <Button onClick={performAuth} className="mt-4">Try Again</Button>
      </div>
    );
  }

  return <MainAppLayout user={user} />;
}
